import React, { Component } from 'react';
import { Modal, Button, Row, Col, Form } from 'react-bootstrap';

export class EditCusModal extends Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        fetch(process.env.REACT_APP_API + 'Customer', {
            method: 'PUT',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                Id: event.target.Id.value,
                Name: event.target.Name.value
            })
        })
            .then(res => res.json())
            .then((result) => {
                alert(result);
            },
                (error) => {
                    alert('Failed');
                })
    }
    render() {
        return (
            <div className="container">

                <Modal
                    {...this.props}
                    size="lg"
                    aria-labelledby="contained-modal-title-vcenter"
                    centered
                >
                    <Modal.Header clooseButton>
                        <Modal.Title id="contained-modal-title-vcenter">
                            Edit Customer
                        </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                        <Row>
                            <Col sm={6}>
                                <Form onSubmit={this.handleSubmit}>
                                    <Form.Group controlId="CustomerId">
                                        <Form.Label>Id</Form.Label>
                                        <Form.Control type="text" name="CustomerId" required
                                            disabled
                                            defaultValue={this.props.depid}
                                            placeholder="CustomerName" />
                                    </Form.Group>

                                    <Form.Group controlId="Name">
                                        <Form.Label>CustomerName</Form.Label>
                                        <Form.Control type="text" name="CustomerName" required
                                            defaultValue={this.props.depname}
                                            placeholder="CustomerName" />
                                    </Form.Group>
                                    <Form.Group controlId="CustomerAddress">
                                        <Form.Label>Address</Form.Label>
                                        <Form.Control type="text" name="CustomerAddress" required
                                            placeholder="Address" />
                                    </Form.Group>

                                    <Form.Group>
                                        <Button variant="primary" type="submit">
                                            Update Customer
                                        </Button>
                                    </Form.Group>
                                </Form>
                            </Col>
                        </Row>
                    </Modal.Body>

                    <Modal.Footer>
                        <Button variant="danger" onClick={this.props.onHide}>Close</Button>
                    </Modal.Footer>

                </Modal>

            </div>
        )
    }

}